.skill-card {
    width: max-content;
    padding: 8px 16px;
    height: 120px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 6px;
    border-radius: 8px;

}

.skill-icon {
    font-size: 80px;
}

.skill-name {
    font-size: 18px;
}

.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.section-title {
    
    font-size: 32px;
    font-weight: 700;
   
}
.skills-section {
    display: flex;
    justify-content: space-between;
    margin: 12px 0px;
}

.skills-section-title {
    font-size: 24px;
    font-weight: 600;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 70%;
    justify-content: flex-end;
}

@media only screen and (max-width:720px) {
    .skills-list {
        max-width: 100%;
        justify-content: center;
    }
    .skills-section {
        flex-direction: column;
        align-items: center;
    }
}